.navbar{
    display: flex;
    justify-content: space-around;
    padding: 16px;
    box-shadow: 0 1px 3px -2px #000;
}
.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
}
.nav-logo img{
    max-width: 100px;
}

.nav-logo p{
    color: #171717;
    font-size: 38px;
    font-weight: 600;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    gap: 50px;
    color: #626262;
    font-size: 20px;
    font-weight: 500;
}
.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content:center ;
    gap: 3px;
    cursor: point;
}
.nav-menu li a{
    color:#171717;
}
.nav-menu hr{
    border:none;
    width: 80%;
    height: 3px;
    border-radius:10px;
    background: #FF4141;
}

.nav-ogin-cart{
    display: flex;
    align-items: center;
    gap:40px;

}
.nav-ogin-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #515151;
    font-size: 20px;
    font-weight: 500;
    background-color: #fff;
    cursor: pointer;
}
.nav-ogin-cart button:active{
    background-color:#f3f3f3 ;
}
.nav-cart-count{
    height: 22px;
    width: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: #FF4141;
    color: #fff;
    

}